<template>
	<div class="about">
		<v-container class="my-1">
			<v-card>
				<v-list subheader>
					<v-subheader>App</v-subheader>
					<v-list-tile ripple @click="enableDevMode">
						<v-list-tile-content>
							<v-list-tile-title>Version</v-list-tile-title>
							<v-list-tile-sub-title class="">{{ version }}</v-list-tile-sub-title>
						</v-list-tile-content>
					</v-list-tile>
				</v-list>
				<!-- Things to display when in DEV mode -->
				<template v-if="devMode">
					<v-divider></v-divider>
					<!-- HSWS versions -->
					<v-list subheader>
						<v-subheader @click="getAbout">HSWS (retreived: {{ aboutAge }})</v-subheader>
						<v-list-tile v-for="product in about" :key="product.name" ripple @click.stop>
							<v-list-tile-content>
								<v-list-tile-title>{{ product.name }}</v-list-tile-title>
								<v-list-tile-sub-title class="">{{ product.version }}</v-list-tile-sub-title>
							</v-list-tile-content>
						</v-list-tile>
					</v-list>
					<v-divider></v-divider>
					<!-- User -->
					<v-list subheader>
						<v-subheader><span v-show="patron.loggedIn">User</span><span v-show="!patron.loggedIn">No user logged in</span></v-subheader>
						<template v-if="patron.loggedIn">
							<v-list-tile ripple @click.stop>
								<v-list-tile-content>
									<v-list-tile-title>Name</v-list-tile-title>
									<v-list-tile-sub-title class="">{{ patron.name }}</v-list-tile-sub-title>
								</v-list-tile-content>
							</v-list-tile>
							<v-list-tile ripple @click.stop>
								<v-list-tile-content>
									<v-list-tile-title>Barcode</v-list-tile-title>
									<v-list-tile-sub-title class="">{{ patron.barcode }}</v-list-tile-sub-title>
								</v-list-tile-content>
							</v-list-tile>
							<v-list-tile ripple @click.stop>
								<v-list-tile-content>
									<v-list-tile-title>Alt ID</v-list-tile-title>
									<v-list-tile-sub-title class="">{{ patron.alternateID }}</v-list-tile-sub-title>
								</v-list-tile-content>
							</v-list-tile>
							<v-list-tile ripple @click.stop>
								<v-list-tile-content>
									<v-list-tile-title>Patron Key</v-list-tile-title>
									<v-list-tile-sub-title class="">{{ patron.patronKey }}</v-list-tile-sub-title>
								</v-list-tile-content>
							</v-list-tile>
							<v-list-tile ripple @click.stop>
								<v-list-tile-content>
									<v-list-tile-title>Session Token</v-list-tile-title>
									<v-list-tile-sub-title class="">{{ patron.sessionToken }}</v-list-tile-sub-title>
								</v-list-tile-content>
							</v-list-tile>
						</template>
					</v-list>
				</template>
			</v-card>
		</v-container>
	</div>
</template>

<script>
var moment = require( 'moment' );

export default {
	name: 'About',
	data ( ) {
		return {
			devModeClicks: 7
		}
	},
	computed: {
		version ( ) {
			return this.$store.state.version;
		},
		about( ) {
			return this.$store.state.about.products;
		},
		aboutAge( ) {
			if ( this.$store.state.about.retreived == -1 ) {
				return 'never';
			}
			return moment( this.$store.state.about.retreived ).fromNow( );
		},
		devMode( ) {
			return this.$store.state.devMode;
		},
		patron( ) {
			return this.$store.state.patron
		}
	},
	methods: {
		enableDevMode( ) {
			if ( this.devMode ) {
				return;
			}
			this.devModeClicks--;
			if ( this.devModeClicks == 0 ) {
				this.$store.dispatch( 'setDevMode', true );
			}
		},
		getAbout( ) {
			this.$store.dispatch( 'about/update' );
		}
	}
}
</script>

