<template>
	<div class="login">
		<v-container class="my-1">
			<v-layout row wrap>
				<v-flex xs12 class="text-xs-center">
					<h1>Sign In</h1>
				</v-flex>
				<v-flex xs12 sm6 offset-sm3 mt-3>
					<form>
						<v-layout column>
							<v-flex>
								<v-text-field v-model="username" label="Library Card" required></v-text-field>
							</v-flex>
							<v-flex>
								<v-text-field v-model="password" label="PIN" type="password" required></v-text-field>
							</v-flex>
							<v-flex class="text-xs-center" mt-2>
								<v-btn block large @click="login" color="primary">Sign In</v-btn>
							</v-flex>
							<v-flex class="text-xs-center" mt-2>
								<v-btn block large @click="logout" color="danger">Logout</v-btn>
							</v-flex>
						</v-layout>
					</form>
				</v-flex>
			</v-layout>
		</v-container>
	</div>
</template>

<script>

export default {
	name: 'Login',
	data ( ) {
		return {
			username: '',
			password: ''
		}
	},
	computed: {
		loggedIn( ) {
			return this.$store.state.patron.loggedIn;
		}
	},
	watch: {
		loggedIn( updated, orginial ) {
			if ( updated ) {
				this.$router.push( { name: 'account' } );
			}
		}
	},
	methods: {
		login( ) {
			this.$store.dispatch( 'patron/login', {
				login: this.username,
				password: this.password
			} );
		},
		logout( ) {
			this.$store.dispatch( 'patron/logout' );
		}
	}
}
</script>

