import Api from '@/services/Api'
import store from '@/store/store'

function getHeaders( ) {
	var headers = { };
	// if logged in add the sessionToken
	if ( store.state.patron.loggedIn ) {
		headers[ 'x-sirs-sessionToken' ] = store.state.patron.sessionToken;
	}
	console.log( headers );
	return headers;
}

export default {
	login( creds ) {
		return Api( ).post( 'v1/user/patron/login', creds );
	},
	logout( ) {
		return Api( ).post( 'v1/user/patron/logout', { }, { headers: getHeaders( ) } );
	},
	getUserDetails( ) {
		return Api( ).get( 'v1/user/patron/key/' + store.state.patron.patronKey, {
			headers: getHeaders( ),
			params: {
				includeFields: 'displayName,alternateID,barcodeList'
			}
		} );
	},
	getItemsOut( ) {
		// circRecordList
		//		checkOutDate
		//		overdue
		//		dueDate
		//		item
		//			barcode
		//			collection
		//			bib
		//				author
		//				title
		// includeFields=circRecordList{checkOutDate,overdue,dueDate,item{barcode,collection,bib{author,title}}}
		return Api( ).get( 'v1/user/patron/key/' + store.state.patron.patronKey, {
			headers: getHeaders( ),
			params: {
				includeFields: 'circRecordList{checkOutDate,overdue,dueDate,item{barcode,collection,bib{author,title}}}'
			}
		} );
	},
	getItemsOnHold( ) {
		// holdRecordList
		//		status
		//		pickupLibrary
		//		queuePosition
		//		beingHeldDate
		//		expirationDate
		//		bib
		//			title
		//			author
		// includeFields=holdRecordList{status,pickupLibrary,queuePosition,beingHeldDate,expirationDate,bib{title,author}}
		return Api( ).get( 'v1/user/patron/key/' + store.state.patron.patronKey, {
			headers: getHeaders( ),
			params: {
				includeFields: 'holdRecordList{status,pickupLibrary,queuePosition,beingHeldDate,expirationDate,bib{title,author}}'
			}
		} );
	}
}
