import Vue from 'vue'
import Vuex from 'vuex'
import about from './modules/about'
import loading from './modules/loading'
import error from './modules/error'
import patron from './modules/patron'

Vue.use( Vuex );

export default new Vuex.Store( {
	modules: {
		about,
		loading,
		error,
		patron
	},
	state: {
		version: '0.0.1',
		devMode: false
	},
	getters: {
	},
	mutations: {
		updateDevMode( state, data ) {
			state.devMode = data;
		}
	},
	actions: {
		setDevMode( context, payload ) {
			context.commit( 'updateDevMode', payload );
		}
	}
} );
