import Vue from 'vue'
import Router from 'vue-router'

// store
import store from '@/store/store'

// pages
import Home from '@/pages/Home.vue'
import Login from '@/pages/Login.vue'
import Logout from '@/pages/Logout.vue'
import About from '@/pages/About.vue'
import Account from '@/pages/Account.vue'
import LibraryCard from '@/pages/Account/LibraryCard.vue'
import ItemsOut from '@/pages/Account/ItemsOut.vue'
import ItemsOnHold from '@/pages/Account/ItemsOnHold.vue'

Vue.use( Router );

const router = new Router( {
	mode: 'history',
	base: process.env.BASE_URL,
	routes: [
		{
			path: '/',
			name: 'home',
			component: Home
		},
		{
			path: '/account',
			name: 'account',
			component: Account,
			meta: {
				requiresAuth: true
			},
			children: [
				{
					path: 'libcard',
					name: 'libcard',
					component: LibraryCard,
					meta: {
					}
				},
				{
					path: 'itemsout',
					name: 'itemsout',
					component: ItemsOut,
					meta: {
					}
				},
				{
					path: 'holds',
					name: 'holds',
					component: ItemsOnHold,
					meta: {
					}
				}
/*
				{
					path: 'history',
					name: 'history',
					component: ItemHistory,
					meta: {
					}
				},
*/
			]
		},
		{
			path: '/login',
			name: 'login',
			component: Login,
			meta: {
				guest: true
			}
		},
		{
			path: 'logout',
			name: 'logout',
			component: Logout,
			meta: {
				requiresAuth: true
			}
		},
		{
			path: '/about',
			name: 'about',
			component: About
		}
	]
} );

router.beforeEach( ( to, from, next ) => {

	const requiresAuth = to.matched.some( record => record.meta.requiresAuth );
	const guest = to.matched.some( record => record.meta.guest );

	console.log( 'to', to );
	console.log( 'requiresAuth', requiresAuth );
	console.log( 'guest', guest );
	console.log( 'loggedIn', store.state.patron.loggedIn );

	// needs to be logged in
	if ( requiresAuth ) {
		// check if logged in, if not kick to login page
		if ( store.state.patron.loggedIn ) {
			// logged in
			next( );
		} else {
			// not logged in
			next( '/login' );
		}
	}
	// needs to not be logged in
	if ( guest ) {
		// check if logged in, if so kick to account page
		if ( store.state.patron.loggedIn ) {
			// logged in
			next( '/account' );
		} else {
			// not logged in
			next( );
		}
	}

	next( );
} );
  
export default router