<template>
	<v-snackbar top auto-height v-model="show" color="error" :timeout="0" >
		{{ message }}
		<v-btn dark flat @click="close" >
			Close
		</v-btn>
	</v-snackbar>
</template>

<script>
export default {
	name: "Error",
	data: function ( ) {
		return {
		}
	},
	computed: {
		show( ) {
			return this.$store.state.error.show;
		},
		message( ) {
			return this.$store.state.error.message;
		}
	},
	methods: {
		close( ) {
			this.$store.dispatch( 'error/hide' );
		}
	}
}
</script>

