<template>
	<div class="logout">
		<v-container class="my-1">
            <h1>Logout page</h1>
			<v-btn block large @click="logout" color="primary">Logout</v-btn>
		</v-container>
	</div>
</template>

<script>

export default {
	name: 'Logout',
	data ( ) {
		return {
		}
	},
	computed: {
		loggedIn( ) {
			return this.$store.state.patron.loggedIn;
		}
	},
	watch: {
		loggedIn( updated, orginial ) {
			if ( !updated ) {
				this.$router.push( { name: 'home' } );
			}
		}
	},
	methods: {
		logout( ) {
			this.$store.dispatch( 'patron/logout' );
		}
	}
}
</script>

