<template>
	<div class="itemsout">
		<v-container class="my-1">
            <h2>Items current on loan</h2>

			<v-tabs centered icons-and-text>
				<v-tabs-slider color="primary"></v-tabs-slider>
					<v-tab href="#calendar">
						Calendar
						<v-icon>calendar_today</v-icon>
					</v-tab>
					<v-tab href="#list">
						List
						<v-icon>list</v-icon>
					</v-tab>
					<v-tab-item value="calendar">
						<v-layout row wrap>
							<v-flex xs12 md6 class="my-2 px-1">
								<v-date-picker ref="picker" v-model="date" :allowed-dates="allowedDates" :events="functionEvents" event-color="red" full-width></v-date-picker>
							</v-flex>
							<v-flex xs12 sm6 class="my-2 px-1">
								<div class="title">Items due back</div>
								<ul class="ma-3">
									<li v-for="note in notes" :key="note">{{ note }}</li>
								</ul>
							</v-flex>
						</v-layout>
					</v-tab-item>
					<v-tab-item value="list">
						<v-btn @click="sortBy('dueDate')">Due date</v-btn>
						<v-btn @click="sortBy('title')">Title</v-btn>
						<v-btn @click="sortBy('barcode')">Barcode</v-btn>
						<v-card>
							<v-list>
								<v-list-tile v-for="item in items" :key="item.id" :class="{error: item.overdue}" ripple @click.stop>
									<v-list-tile-content>
										<v-list-tile-title>{{ item.title }} <span v-if="item.author"> by {{ item.author }}</span></v-list-tile-title>
										<v-list-tile-sub-title class="">{{ daysDue( item.dueDate ) }} - {{ item.barcode }}</v-list-tile-sub-title>
									</v-list-tile-content>
								</v-list-tile>
							</v-list>
						</v-card>
					</v-tab-item>
			</v-tabs>

		</v-container>
	</div>
</template>

<script>
var moment = require( 'moment' );

export default {
	name: 'ItemsOut',
	data ( ) {
		return {
			date: new Date( ).toISOString( ).substr( 0, 10 ),
			pickerDate: null,
			notes: [ ]
		}
	},
	computed: {
		items( ) {
			return this.$store.state.patron.itemsOut.data;
		}
	},
	methods: {
		sortBy( prop ) {
			this.items.sort( ( a,b ) => a[ prop ] < b[ prop ] ? -1 : 1 );
		},
		daysDue( date ) {
			return moment( date ).fromNow( );
		},
		allowedDates( date ) {
			// this is a really nasy way of doing it
			var itemDue = false;
			this.items.forEach( function( item ) {
				if ( moment( date ).isSame( item.dueDate, 'day' ) ) {
					itemDue = true;
				}
			} );
			return itemDue;
		},
		functionEvents( date ) {
			// this is a really nasy way of doing it
			var itemDue = false;
			this.items.forEach( function( item ) {
				if ( moment( date ).isSame( item.dueDate, 'day' ) ) {
					itemDue = item.overdue ? 'red' : 'blue';
				}
			} );
			return itemDue;
		}
	},
	watch: {
		date( val ) {
			// this is a really nasty way of doing it
			var newNotes = [ ];
			this.items.forEach( function( item ) {
				if ( moment( val ).isSame( item.dueDate, 'day' ) ) {
					newNotes.push( item.title + ' (' + item.barcode + ')' );
				}
			} );
			this.notes = newNotes;
		}
	}
}
</script>

