import PatronService from '@/services/PatronService'

export default {
	namespaced: true,
	state: {
		loggedIn: false,
		retreived: -1,
		sessionToken: null,
		patronKey: '',
		name: '',
		barcode: '',
		alternateID: '',
		itemsOut: {
			retreived: -1,
			data: [ ]
		},
		itemsOnHold: {
			retreived: -1,
			data: [ ]
		},
		borrowingHistory: {
			retreived: -1,
			data: [ ]
		}
	},
	getters: {
	},
	mutations: {
		login( state, data ) {
//			state.retreived = Date.now( );
			state.sessionToken = data.sessionToken;
			state.name = data.name;
			state.patronKey = data.patronKey;
			state.loggedIn = true;
		},
		logout( state, data ) {
			state.loggedIn = false;
			state.retreived = -1;
			state.sessionToken = null;
			state.patronKey = '';
			state.name = '';
			state.barcode = '';
			state.alternateID = '';
			state.itemsOut = {
				retreived: -1,
				data: [ ]
			};
			state.itemsOnHold = {
				retreived: -1,
				data: [ ]
			};
			state.borrowingHistory = {
				retreived: -1,
				data: [ ]
			};
		},
		updatePatron( state, data ) {
			state.retreived = Date.now( );
			state.name = data.name;
			state.barcode = data.barcode;
			state.alternateID = data.alternateID;
		},
		setItemsOut( state, data ) {
			state.itemsOut.retreived = Date.now( );
			state.itemsOut.data = data;
		},
		setItemsOnHold( state, data ) {
			state.itemsOnHold.retreived = Date.now( );
			state.itemsOnHold.data = data;
		}

	},
	actions: {
		login( context, payload ) {
			context.dispatch( 'error/hide', null, { root: true } );
			context.dispatch( 'loading/show', { message: 'Logging in' }, { root: true } );

			PatronService.login( payload )
				.then( ( response ) => {
					console.log( response.data );
					context.commit( 'login', response.data );
//					context.dispatch( 'getBarcode' );
				} )
				.catch( ( error ) => {
					var errorMessage = 'Something went wrong';
					if ( error.response ) {
						// The request was made and the server responded with a status code
						// that falls out of the range of 2xx
						console.log( error.response.data );
						console.log( error.response.status );
						console.log( error.response.headers );
						if ( error.response.status == 401 ) {
							errorMessage = 'Invalid Library Card or PIN number';
						} else {
							errorMessage = 'Server returned: ' + error.response.status + ' => ' + JSON.stringify( error.response.data );
						}
					} else if ( error.request ) {
						// The request was made but no response was received
						// `error.request` is an instance of XMLHttpRequest in the browser and an instance of
						// http.ClientRequest in node.js
//						console.log( error.request );
						errorMessage = 'No response from server';
					} else {
						// Something happened in setting up the request that triggered an Error
						console.log( 'Error', error.message );
					}
//					console.log( error.config );

					context.dispatch( 'error/show', errorMessage, { root: true } );
				} )
				.finally( ( ) => {
					context.dispatch( 'loading/hide', null, { root: true } );
				} );
		},
		logout( context, payload ) {
			context.dispatch( 'error/hide', null, { root: true } );
			context.dispatch( 'loading/show', { message: 'Logging out' }, { root: true } );

			PatronService.logout( )
				.then( ( response ) => {
					context.commit( 'logout', response.data );
				} )
				.catch( ( error ) => {
					var errorMessage = 'Something went wrong';
					if ( error.response ) {
						// The request was made and the server responded with a status code
						// that falls out of the range of 2xx
						console.log( error.response.data );
						console.log( error.response.status );
						console.log( error.response.headers );
						errorMessage = 'Server returned: ' + error.response.status + ' => ' + JSON.stringify( error.response.data );
					} else if ( error.request ) {
						// The request was made but no response was received
						// `error.request` is an instance of XMLHttpRequest in the browser and an instance of
						// http.ClientRequest in node.js
//						console.log( error.request );
						errorMessage = 'No response from server';
					} else {
						// Something happened in setting up the request that triggered an Error
						console.log( 'Error', error.message );
					}
//					console.log( error.config );

					context.dispatch( 'error/show', errorMessage, { root: true } );
				} )
				.finally( ( ) => {
					context.dispatch( 'loading/hide', null, { root: true } );
				} );
		},
		updatePatron( context, payload ) {
			context.dispatch( 'error/hide', null, { root: true } );
			context.dispatch( 'loading/show', { message: 'Retreving user info' }, { root: true } );

			PatronService.getUserDetails( )
				.then( ( response ) => {
					var data = {
						name: response.data.fields.displayName,
						alternateID: response.data.fields.alternateID,
						barcode: response.data.fields.barcodeList[0].fields.barcode
					};
					context.commit( 'updatePatron', data );
				} )
				.catch( ( error ) => {
					var errorMessage = 'Something went wrong';
					if ( error.response ) {
						// The request was made and the server responded with a status code
						// that falls out of the range of 2xx
						console.log( error.response.data );
						console.log( error.response.status );
						console.log( error.response.headers );
						errorMessage = 'Server returned: ' + error.response.status + ' => ' + JSON.stringify( error.response.data );
					} else if ( error.request ) {
						// The request was made but no response was received
						// `error.request` is an instance of XMLHttpRequest in the browser and an instance of
						// http.ClientRequest in node.js
//						console.log( error.request );
						errorMessage = 'No response from server';
					} else {
						// Something happened in setting up the request that triggered an Error
						console.log( 'Error', error.message );
					}
//					console.log( error.config );

					context.dispatch( 'error/show', errorMessage, { root: true } );
				} )
				.finally( ( ) => {
					context.dispatch( 'loading/hide', null, { root: true } );
				} );
		},
		updateItemsOut( context, payload ) {
			context.dispatch( 'error/hide', null, { root: true } );
			context.dispatch( 'loading/show', { message: 'Retreving items out' }, { root: true } );

			PatronService.getItemsOut( )
				.then( ( response ) => {
					var data = [ ];
					// need to really mangle the data to make is usable
					// for each circRecord in response.data.fields.circRecordList
					response.data.fields.circRecordList.forEach( function( circRecord ) {
						var item = {
							id: circRecord.key,
							checkOutDate: circRecord.fields.checkOutDate,
							overdue: circRecord.fields.overdue,
							dueDate: circRecord.fields.dueDate,
							barcode: circRecord.fields.item.fields.barcode,
							collection: circRecord.fields.item.fields.collection.key,
							bibId: circRecord.fields.item.fields.bib.key,
							title: circRecord.fields.item.fields.bib.fields.title,
							author: circRecord.fields.item.fields.bib.fields.author
						};
						data.push( item );
					} );
					context.commit( 'setItemsOut', data );

				} )
				.catch( ( error ) => {
					var errorMessage = 'Something went wrong';
					if ( error.response ) {
						// The request was made and the server responded with a status code
						// that falls out of the range of 2xx
						console.log( error.response.data );
						console.log( error.response.status );
						console.log( error.response.headers );
						errorMessage = 'Server returned: ' + error.response.status + ' => ' + JSON.stringify( error.response.data );
					} else if ( error.request ) {
						// The request was made but no response was received
						// `error.request` is an instance of XMLHttpRequest in the browser and an instance of
						// http.ClientRequest in node.js
//						console.log( error.request );
						errorMessage = 'No response from server';
					} else {
						// Something happened in setting up the request that triggered an Error
						console.log( 'Error', error.message );
					}
//					console.log( error.config );

					context.dispatch( 'error/show', errorMessage, { root: true } );
				} )
				.finally( ( ) => {
					context.dispatch( 'loading/hide', null, { root: true } );
				} );
		},
		updateItemsOnHold( context, payload ) {
			context.dispatch( 'error/hide', null, { root: true } );
			context.dispatch( 'loading/show', { message: 'Retreving items out' }, { root: true } );

			PatronService.getItemsOnHold( )
				.then( ( response ) => {
					var data = [ ];
					// need to really mangle the data to make is usable
					// for each holdRecord in response.data.fields.holdRecordList
					response.data.fields.holdRecordList.forEach( function( holdRecord ) {
						var item = {
							id: holdRecord.key,
							status: holdRecord.fields.status,
							queuePosition: holdRecord.fields.queuePosition,
							beingHeldDate: holdRecord.fields.beingHeldDate,
							expirationDate: holdRecord.fields.expirationDate,
							pickupLibrary: holdRecord.fields.pickupLibrary.key,
							bibId: holdRecord.fields.bib.key,
							title: holdRecord.fields.bib.fields.title,
							author: holdRecord.fields.bib.fields.author
						};
						data.push( item );
					} );
					context.commit( 'setItemsOnHold', data );

				} )
				.catch( ( error ) => {
					var errorMessage = 'Something went wrong';
					if ( error.response ) {
						// The request was made and the server responded with a status code
						// that falls out of the range of 2xx
						console.log( error.response.data );
						console.log( error.response.status );
						console.log( error.response.headers );
						errorMessage = 'Server returned: ' + error.response.status + ' => ' + JSON.stringify( error.response.data );
					} else if ( error.request ) {
						// The request was made but no response was received
						// `error.request` is an instance of XMLHttpRequest in the browser and an instance of
						// http.ClientRequest in node.js
//						console.log( error.request );
						errorMessage = 'No response from server';
					} else {
						// Something happened in setting up the request that triggered an Error
						console.log( 'Error', error.message );
					}
//					console.log( error.config );

					context.dispatch( 'error/show', errorMessage, { root: true } );
				} )
				.finally( ( ) => {
					context.dispatch( 'loading/hide', null, { root: true } );
				} );
		}

	}
}