export default {
	namespaced: true,
	state: {
		show: false,
		message: 'Loading',
		progressColor: 'primary'
	},
	getters: {
	},
	mutations: {
		setShow( state, data ) {
			state.show = data;
		},
		setMessage( state, data ) {
			state.message = data;
		},
		setProgressColor( state, data ) {
			state.progressColor = data;
		}
	},
	actions: {
		toggle( context, payload ) {
			context.commit( 'setShow', !context.state.show );
		},
		show( context, payload ) {
			// check if the payload is not null
			if ( payload !== null ) {
				// check for message
				if ( payload.hasOwnProperty( 'message' ) ) {
					context.commit( 'setMessage', payload.message );
				}
				// check for progressColor
				if ( payload.hasOwnProperty( 'setProgressColor' ) ) {
					context.commit( 'setProgressColor', payload.progressColor );
				}
			}
			context.commit( 'setShow', true );
		},
		hide( context, payload ) {
			context.commit( 'setShow', false );
		}
	}
}