<template>
	<div class="account">
		<v-container class="my-1">
			<h1>Hello {{ name }}
				<v-btn icon fab right @click="updateData">
					<v-icon>refresh</v-icon>
				</v-btn>
			</h1>

			<v-alert v-model="displayOverdue" type="error">
				You have {{ overdueCount }} items that are overdue
			</v-alert>
			<router-view></router-view>
		</v-container>
	</div>
</template>

<script>
import Config from '@/config'

var moment = require( 'moment' );

export default {
	name: 'Account',
	data ( ) {
		return {
		}
	},
	created( ) {
		// check if we need to update data
		// user
		if ( this.$store.state.patron.retreived == -1 || moment( ).isAfter( moment( this.$store.state.patron.retreived ).add( Config.dataAge.user.automatic ) ) ) {
			this.updateUser( );
		}
		// items out
		if ( this.$store.state.patron.itemsOut.retreived == -1 || moment( ).isAfter( moment( this.$store.state.patron.itemsOut.retreived ).add( Config.dataAge.itemsOut.automatic ) ) ) {
			this.updateItemsOut( );
		}
		// items on hold
		if ( this.$store.state.patron.itemsOnHold.retreived == -1 || moment( ).isAfter( moment( this.$store.state.patron.itemsOnHold.retreived ).add( Config.dataAge.itemsOnHold.automatic ) ) ) {
			this.updateItemsOnHold( );
		}
	},
	computed: {
		overdueItems( ) {
			return this.$store.state.patron.itemsOut.data.filter( item => {
				return item.overdue
			} );
		},
		overdueCount( ) {
			return this.overdueItems.length;
		},
		displayOverdue( ) {
			return this.overdueCount == 0 ? false : true;
		},
		name( ) {
			return this.$store.state.patron.name;
		}
	},
	methods: {
		updateUser( ) {
			this.$store.dispatch( 'patron/updatePatron' );
		},
		updateItemsOut( ) {
			this.$store.dispatch( 'patron/updateItemsOut' );
		},
		updateItemsOnHold( ) {
			this.$store.dispatch( 'patron/updateItemsOnHold' );
		},
		daysDue( date ) {
			return moment( date ).fromNow( );
		},
		updateData( ) {
			// check if we need to update data
			// user
			if ( this.$store.state.patron.retreived == -1 || moment( ).isAfter( moment( this.$store.state.patron.retreived ).add( Config.dataAge.user.manual ) ) ) {
				this.updateUser( );
			}
			// items out
			if ( this.$store.state.patron.itemsOut.retreived == -1 || moment( ).isAfter( moment( this.$store.state.patron.itemsOut.retreived ).add( Config.dataAge.itemsOut.manual ) ) ) {
				this.updateItemsOut( );
			}
			// items on hold
			if ( this.$store.state.patron.itemsOnHold.retreived == -1 || moment( ).isAfter( moment( this.$store.state.patron.itemsOnHold.retreived ).add( Config.dataAge.itemsOnHold.manual ) ) ) {
				this.updateItemsOnHold( );
			}	
		}
	}
}</script>

